import React from 'react';
import { withSiteData } from 'react-static';

import Cta from '@components/Cta';
import UseCases from '@components/Join/UseCases';
import HowMarketWorks from '@components/Join/HowMarketWorks';
import MostPeople from '@components/Join/MostPeople';
import ExplainerVideo from '@components/Join/ExplainerVideo';
import Hero from '@components/Join/Hero';

interface HomeComponentProps {
  subscriptionPopUpVisible?: boolean;
  enableVideo?: boolean;
  email?: string;
}

export const HomeComponent = ({
  subscriptionPopUpVisible,
  enableVideo,
  email
}: HomeComponentProps) => (
  <div>
    <Hero email={email} />
    <MostPeople />
    <UseCases />
    <HowMarketWorks />
    <Cta subscriptionPopUpVisible={subscriptionPopUpVisible} bg="#f0f0f0" />
  </div>
);

export default withSiteData(HomeComponent);



