import React from 'react';
import {
  ContainerWrapper,
  MarketButton,
  SecondaryDescription,
  SecondaryHeader,
  Section,
  SectionHeader,
  UseCaseIllustration
} from '@src/Styles';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import { size } from '@src/breakpoints';
import { Link } from 'react-static';

// Images
import risk2x from '@images/join/risk@2x.png';
import risk from '@images/join/risk@1x.png';
import anything2x from '@images/join/anything@2x.png';
import anything from '@images/join/anything@1x.png';
import leverage2x from '@images/join/leverage@2x.png';
import leverage from '@images/join/leverage@1x.png';
import short2x from '@images/join/short@2x.png';
import short from '@images/join/short@1x.png';

// Styles
const UseCaseContainer = styled.div`
  text-align: center;
  padding: 0 15px;
  min-height: 420px;

  p {
    margin-top: 15px;
  }
`;

const ActionButton = MarketButton.extend`
  margin-top: 50px;

  @media (max-width: ${size.tabletL}) {
    margin-top: 20px;
  }
`;

const UseCases = () => {
  return (
    <Section bg={'#F0F0F0'}>
      <ContainerWrapper>
        <Row type="flex" justify="center" align="middle">
          <SectionHeader color={'#000'}>Use Cases</SectionHeader>
        </Row>
        <Row type="flex" justify="center" align="middle">
          <Col sm={20} md={12} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <img
                  alt="Trade with Leverage"
                  src={leverage}
                  srcSet={`${leverage}, ${leverage2x} 2x`}
                />
              </UseCaseIllustration>
              <SecondaryHeader>Trade with Leverage</SecondaryHeader>
              <SecondaryDescription>
                Get the same price exposure with less collateral.
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={20} md={12} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <img
                  alt="Short Crypto Assets"
                  src={short}
                  srcSet={`${short}, ${short2x} 2x`}
                />
              </UseCaseIllustration>
              <SecondaryHeader>Short Crypto Assets</SecondaryHeader>
              <SecondaryDescription>
                Profit from a decline in price, with no margin or borrowing.
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={20} md={12} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <img
                  alt="Trade Anything"
                  src={leverage}
                  srcSet={`${anything}, ${anything2x} 2x`}
                />
              </UseCaseIllustration>
              <SecondaryHeader>Trade Anything</SecondaryHeader>
              <SecondaryDescription>
                Buy or sell a share of stock, barrel of oil or bar of gold.
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={20} md={12} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <img
                  alt="Remove Price Risk"
                  src={risk}
                  srcSet={`${risk}, ${risk2x} 2x`}
                />
              </UseCaseIllustration>
              <SecondaryHeader>Remove Price Risk</SecondaryHeader>
              <SecondaryDescription>
                Hedge against an unexpected drop in the price of your assets.
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
        </Row>
        <Row type="flex" justify="center" align="middle">
          <Link to="/about">
            <ActionButton bg={'#000'} color={'#00FFE2'}>
              Learn more
            </ActionButton>
          </Link>
        </Row>
      </ContainerWrapper>
    </Section>
  );
};

export default UseCases;



