import React from 'react';
import {
  ContainerWrapper,
  Description,
  MarketButton,
  SecondaryDescription,
  SecondaryHeader,
  Section,
  SectionHeader,
  UseCaseIllustration
} from '@src/Styles';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import { size } from '@src/breakpoints';
import { Link } from 'react-static';

// Images
import highQuality from '@images/join/access.svg';
import buildWealth from '@images/join/buildwealth.svg';
import hedgeRisk from '@images/join/hedge.svg';
import investSafely from '@images/join/invest.svg';

// Styles
const UseCaseContainer = styled.div`
  text-align: center;
  padding: 0 15px;
  min-height: 220px;

  p {
    margin-top: 15px;
  }
`;

const BoldText = styled.span`
  font-weight: 700;
`;

const ActionButton = MarketButton.extend`
  margin-top: 50px;

  @media (max-width: ${size.tabletL}) {
    margin-top: 20px;
  }
`;

const DerivativesDescription = Description.extend`
  text-align: center;
  margin-top: 35px;
  line-height: 33px;

  @media (max-width: 540px) {
    text-align: left;
    line-height: 22px;
  }
`;

const MostPeople = () => {
  return (
    <Section bg={'#fff'}>
      <ContainerWrapper>
        <Row type="flex" justify="center" align="middle">
          <SectionHeader color={'#000'}>Most People ...</SectionHeader>
        </Row>
        <Row type="flex" justify="center" align="middle">
          <Col sm={6} md={12} lg={6}>
            <UseCaseContainer>
              <img alt="Cant access high quality assets" src={highQuality} />
              <SecondaryDescription>
                Cant access <br /> high quality assets
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={6} md={12} lg={6}>
            <UseCaseContainer>
              <img alt="Cant build wealth" src={buildWealth} />
              <SecondaryDescription>
                Can't build <br /> wealth
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={12} md={12} lg={6}>
            <UseCaseContainer>
              <img alt="Cant hedge or manage risk" src={hedgeRisk} />
              <SecondaryDescription>
                Cant hedge or <br /> manage risk
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
          <Col sm={12} md={12} lg={6}>
            <UseCaseContainer>
              <img alt="Cant invest safely" src={investSafely} />
              <SecondaryDescription>
                Cant invest <br /> safely
              </SecondaryDescription>
            </UseCaseContainer>
          </Col>
        </Row>
        <DerivativesDescription color={'#000'}>
          This results in lost opportunities and unnecessary risks. We are using
          blockchain technology to redefine the financial landscape. Meet{' '}
          <BoldText>MARKET Protocol</BoldText>, a framework for creating
          Position Tokens that track the price of any asset.
        </DerivativesDescription>
        <Row type="flex" justify="center" align="middle">
          <Link
            to="https://marketprotocol.io/assets/MARKET_Protocol-Whitepaper.pdf"
            target="_blank"
          >
            <ActionButton bg={'#000'} color={'#00FFE2'}>
              Read our whitepaper
            </ActionButton>
          </Link>
        </Row>
      </ContainerWrapper>
    </Section>
  );
};

export default MostPeople;



