import React from 'react';
import {
  ContainerWrapper,
  MarketButton,
  MarketList,
  SecondaryHeader,
  Section,
  SectionHeader,
  UseCaseIllustration
} from '@src/Styles';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import { Link } from 'react-static';

// Images
import trade from '@images/join/trade.svg';
import exit from '@images/join/exit.svg';
import mint from '@images/join/mint.svg';
import { device, size } from '@src/breakpoints';

// Styles
const UseCaseContainer = styled.div`
  text-align: center;
  padding: 0 15px;
  min-height: 490px;

  p {
    margin-top: 15px;
  }

  @media ${device.tablet} and (max-width: ${size.tabletL}) {
    display: inline-flex;
    align-items: center;
    min-height: 250px;
    width: 100%;
    text-align: left;
    text-align: left;
  }
`;

const UseCaseDesc = styled.div`
  @media ${device.tablet} and (max-width: ${size.tabletL}) {
    margin-top: -25px;
    padding-left: 35px;
  }
`;

const List = MarketList.extend`
  font-size: 16px;
  font-weight: 400;
  text-align: left;

  :before {
    width: 5px;
    height: 5px;
    background-color: #000;
    margin-left: -15px;
  }

  @media ${device.desktopS} {
    font-size: 16px;
  }
`;

const ActionButton = MarketButton.extend`
  margin-top: 50px;

  @media (max-width: ${size.tablet}) {
    margin-top: 0;
  }
`;

const HowMarketWorks = () => {
  return (
    <Section bg={'#fff'}>
      <ContainerWrapper>
        <Row type="flex" justify="center" align="middle">
          <SectionHeader color={'#000'}>
            How MARKET Protocol Works
          </SectionHeader>
        </Row>
        <Row type="flex" justify="center" align="middle">
          <Col sm={20} md={20} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <span>1</span>
                <img alt="Create a Contract" src={mint} />
              </UseCaseIllustration>
              <UseCaseDesc>
                <SecondaryHeader>Mint Tokens</SecondaryHeader>
                <List>
                  Anyone can mint Position Tokens by depositing collateral
                </List>
                <List>
                  Collateral remains on-chain for the duration of trading
                </List>
              </UseCaseDesc>
            </UseCaseContainer>
          </Col>
          <Col sm={20} md={20} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <span>2</span>
                <img alt="Trade a Contract" src={trade} />
              </UseCaseIllustration>
              <UseCaseDesc>
                <SecondaryHeader>Trade</SecondaryHeader>
                <List>
                  Once minted, Position Tokens trade on any exchange or
                  application
                </List>
                <List>Always solvent, no margin calls, safe leverage</List>
              </UseCaseDesc>
            </UseCaseContainer>
          </Col>
          <Col sm={20} md={20} lg={6}>
            <UseCaseContainer>
              <UseCaseIllustration>
                <span>3</span>
                <img alt="Exit the Position" src={exit} />
              </UseCaseIllustration>
              <UseCaseDesc>
                <SecondaryHeader>Exit the Position</SecondaryHeader>
                <List>Easily trade with another user</List>
                <List>Or hold a Position Token until expiration</List>
              </UseCaseDesc>
            </UseCaseContainer>
          </Col>
        </Row>
        <Row type="flex" justify="center" align="middle">
          <Link to="/about">
            <ActionButton bg={'#000'} color={'#00FFE2'}>
              Learn more
            </ActionButton>
          </Link>
        </Row>
      </ContainerWrapper>
    </Section>
  );
};

export default HowMarketWorks;



